.mainTitle {
  font-weight: 700;
  line-height: 140%;
  font-size: 25px;
}

.title {
  font-size: 18px;
  font-weight: 600;
}

.personalInfo {
  display: flex;
  flex-direction: column;
  width: 20%;
  background-color: rgb(28 27 27);
  color: #FFFFFF;
  padding: 32px;
}
/*
.personalInfo div:not(:last-child) {
  margin-top: 20px;
} */

.positionTitle {
  font-weight: 700;
  line-height: 77px;
  font-size: 35px;
}

.paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 22.4px;
  margin-top: 3px;
}

.personalTitle {
  font-size: 16px;
  font-weight: 600;
  line-height: 140%;
}

.personalInfoDiv {
  margin-top: 15px;
}

.cv {
  padding: 2px 17px;
  cursor: pointer;
}

.jobTitle {
  font-weight: 600;
  font-size: 15px;
  line-height: 22.4px;
  margin-top: 10px;
}

.sectionTitle {
  font-weight: 700;
  line-height: 140%;
  font-size: 17px;
  margin: 30px 0 10px 0;
  text-decoration: underline;
}

.subInfo {
  font-weight: 600;
  font-size: 13px;
  line-height: 22.4px;
}

.paragraph2 {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

#list {

}

#list > .a:not(:last-child) {
  margin-bottom: 25px;
}

#list > .jobTitle:not(:last-child) {
  margin-bottom: 25px;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 50px;
  font-weight: bold;
  top: 5px;
  position: absolute;
  right: 20px;
}

.close:hover,
.close:focus {
  /* color: #000; */
  text-decoration: none;
  cursor: pointer;
}

#image {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
